.profile-container{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-height: fit-content;
    background-image: url("../../assets/Home/bg.jpg");
}

.profile-parent{
    display: flex;
    align-items: center;
    color: aliceblue;
}

.profile-details-name{
    font-size: 24px;
    font-family: "Poppins SemiBold", sans-serif;
}

.profile-details-role{
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
}

.profile-details-role h1{
    font-size: 40px;
    font-family: sans;
}

.primary-title{
    font-size: 25px;
}

.primary-text{
    color: var(--white);
}

.highlighted-text{
    color: var(--dark-orange);
}

.profile-role-tagline{
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light", sans-serif;
}

.profile-options .highlighted-btn{
    margin: 0 0 0 28px;
}

.profile-options .highlighted-btn:hover{
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #111;
    transform: translateY(-2px);
    transition: 0.3s ease-out;
}

.profile-picture{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 330px;
    width: 330px;
    margin-left: 50px;
}

.profile-picture-background{
    height: 80%;
    width: 80%;
    background-image: url("../../assets/Home/me.jpeg");
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
    transform: scale(1);
    transition: 0.3s ease-out;
}

.profile-picture-background:hover{
    transform: scale(1.12);
    transition: 0.3s ease-out;
}

.colz .colz-icon i{
    color: white;
    padding: 8px 8px 8px 0px;
    margin: 2px;
}

.colz .colz-icon i:hover{
    transform: scale(1.5);

}

@media screen and (max-width: 1110px){
    .profile-picture{
        margin: 0 0 0 80px;
    }

    .profile-details-name{
        font-size: 20px;
    }

    .profile-details-role .primary-text{
        font-size: 12px;
    }

    .profile-options .btn{
        width: 120px;
        padding: 11px 0;
        z-index: 700;
    }
}

@media screen and (max-width: 910px){
    .profile-picture{
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
}

@media screen and (max-width: 810px){
    .profile-picture{
        margin: 0 !important;
    }

    .profile-parent{
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }

    .profile-options{
        display: flex;
        justify-content: space-around;
    }

    .profile-details{
        margin: 25px 0 0 0;
    }

    .profile-options .highlighted-btn{
        margin: 0 !important;
    }

    .profile-options .profile-details-role{
        text-align: center;
    }

    .primary-title{
        font-size: 18px;
    }

    .profile-details-role h1{
        font-size: 28px;
        font-family: sans;
    }

    .profile-role-tagline{
        font-size: 14px;
        margin: 5px 0 0 0;
        font-family: "Poppins Light", sans-serif;
    }
}

@media screen and (max-width: 375px){
    .profile-picture{
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }

    .profile-details{
        width: 90%
    }
}
